
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
.child-view{transition:transform 0.5s cubic-bezier(0.55, 0, 0.1, 1),opacity 0.5s cubic-bezier(0.55, 0, 0.1, 1)
}
.slide-left-enter-active{height:100%
}
.slide-left-enter,.slide-right-leave-active{opacity:0;position:absolute;width:100%;height:100% !important;transform:translate(30px, 0)
}
.slide-left-enter #newItem{opacity:0 !important
}
.slide-left-enter-active .empty,.slide-right-enter-active .empty{opacity:0 !important
}
.slide-right-enter-active{height:100% !important
}
.slide-left-leave-active,.slide-right-enter{opacity:0;top:0;width:100%;height:100%;position:absolute;transform:translate(-30px, 0)
}



